import React from 'react'
import './Barra.css';
import { NavLink } from 'react-router-dom';

import iniciandorevolucion1 from '../../../images/blog/iniciorevolucion.jpeg';
import evolucionSIG1 from '../../../images/blog/evolucionSIG.jpg';
import selecciondesitios1 from '../../../images/blog/selecciondesitios.jpeg';

export const Barra = () => {
  const articles = [
    {
      id: 1,
      backgroundImage: iniciandorevolucion1,
      title: "La Revolucin Geoespacial",
      subtitle: "ARTCULO UNO",
      description: "Descubre cmo la tecnologa geoespacial est transformando el mundo moderno y las oportunidades que ofrece para el futuro.",
      link: "/La_Revolucion_Geoespacial_solo_esta_iniciando"
    },
    {
      id: 2,
      backgroundImage: evolucionSIG1,
      title: "Evolucin de los SIG",
      subtitle: "ARTCULO DOS", 
      description: "Explora la evolucin histrica de los Sistemas de Informacin Geogrfica y su impacto en diferentes industrias.",
      link: "/Evolucion_de_los_SIG"
    },
    {
      id: 3,
      backgroundImage: selecciondesitios1,
      title: "Seleccin de Sitios con GIS",
      subtitle: "ARTCULO TRES",
      description: "Aprende sobre la importancia de los GIS en la seleccin estratgica de sitios para proyectos de desarrollo.",
      link: "/Importancia de los GIS en la seleccin de sitios"
    }
  ];

  return (
    <div className="minimal-articles-container">
      <div className="row">
        {articles.map((article) => (
          <div key={article.id} className="col-md-4 mb-4">
            <div className="minimal-article">
              <div 
                className="article-image"
                style={{ backgroundImage: `url(${article.backgroundImage})` }}
              ></div>
              
              <div className="article-content">
                <h3 className="article-title">{article.title}</h3>
                <p className="article-description">{article.description}</p>
                
                <NavLink to={article.link} className="article-link">
                  Leer ms 
                </NavLink>
              </div>
            </div>
          </div>
        ))}
      </div>
    </div>
  )
}

