import React from 'react'
import './Estilo_Articulo.css';
import { Footer } from '../../Footer/Footer';
import { Barra } from './Barra';
import MapaCiclista from '../../MapaCiclista';


export const Mapaciclistapuebla = () => {
  return (
    <div>
      {/* Cuerpo Articulo */}
      <div className="container articulocuerpo">
        {/* Mapa Interactivo en la parte superior - Ancho completo */}
        <div className="row mb-5">
          <div className="col-12">
            <h1 className="mb-3">Mapa Ciclista de Puebla</h1>
            <p className="mb-4 text-muted">
              Explora la red de ciclovas y carriles bici de Puebla. Haz clic en cualquier elemento para obtener informacin detallada.
            </p>
            <MapaCiclista />
          </div>
        </div>

        {/* Contenido del artculo - Texto reducido */}
        <div className="row mb-5">
          <div className="col-md-8">
            <article>
              <h2 className="mb-4">Navegando las Rutas Urbanas</h2>
              <p className="mb-4">
                Los mapas ciclistas son herramientas esenciales para promover el transporte sostenible en las ciudades modernas, permitiendo a los ciclistas encontrar rutas seguras y eficientes.
              </p>

              <h3 className="mb-3">Caractersticas principales</h3>
              <p className="mb-4">
                Incluyen informacin sobre ciclovas, carriles bici, pendientes, estaciones de bicicletas pblicas y estacionamientos seguros para una navegacin ptima.
              </p>

              <h3 className="mb-3">Beneficios urbanos</h3>
              <p className="mb-5">
                Fomentan el uso de la bicicleta como transporte, reduciendo la congestin vehicular y las emisiones contaminantes hacia una movilidad ms sostenible.
              </p>
            </article>
          </div>
        </div>

        {/* Artculos relacionados */}
        <div className="row">
          <div className="col-12">
            <h2 className="mb-4">Artculos relacionados</h2>
            <Barra />
          </div>
        </div>
      </div>

      <Footer />
    </div>
  )
}



